import { fetchUrl, formatDuration, formatTimestampToAMPM, getToken, redirectTo, removeOptions } from "../util";
import { closeModal, openModal } from "../util/modal";
import {
  XANO_COACH_GET_COACHES_URL,
  XANO_COACH_DASHBOARD_REDIRECT,
  XANO_COACH_SIGN_IN_REDIRECT,
  XANO_COACH_SESSION_SUMMARY_URL,
  XANO_COACH_SESSION_SEARCH_URL,
  XANO_COACH_UPDATE_COMMENTS_URL,
  XANO_COACH_UPDATE_SECOND_COACH_URL,
} from "../util/xano_urls";

let sessionId;
let modalUsers = [];

window.onload = function () {
 const token = getToken();
  if (token) {
    getSessionData(token);
  } else {
    // ? if there is no token
    redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

async function getSessionData(token) {
  var curUrl = new URL(document.location.href);
  sessionId = curUrl.searchParams.get("session"); // ? gets the session id from the url

  if (!sessionId) redirectTo(XANO_COACH_DASHBOARD_REDIRECT);

  let data = await fetchUrl(XANO_COACH_SESSION_SUMMARY_URL + sessionId, "GET", false, token);

  if (data.isFetchOk !== undefined && !data.isFetchOk) {
    console.error(data.message);
    // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
    return;
  }
  // ? if the session is not in progress, dont continue
  if (data.status !== "COMPLETED") {
    console.error("Session hasnt been completed yet!");
    redirectTo(XANO_COACH_DASHBOARD_REDIRECT);
    return;
  }

  let attended = [],
    notAttended = [];
  data?.users?.forEach((user) => {
    if (user.attended) attended.push(user);
    else notAttended.push(user);
  });

  // ? if the session is in progress and the coach is in session (xano already checking if session is the coach's session)
  appendSessionHeader(data, attended.length);
  appendAttendedSessions(attended);
  appendMissedSessions(notAttended);

  initEditUserBtn(data.users);
  initAddCoachBtn(data.secondary_coach, token);
  initAddNotesBtn(data.comments, token);
}

function appendSessionHeader(session, attended) {
  const headerContainer = document.getElementById("header-container");

  headerContainer.querySelector("#session-name").innerText = session.session_name;
  headerContainer.querySelector("#start-time").innerText = formatTimestampToAMPM(session.start_time, true) ?? "NaN";
  headerContainer.querySelector("#date").innerText = new Date(session.time).toLocaleDateString("en-us", {
    weekday: "short",
    month: "short",
    day: "numeric",
  });
  headerContainer.querySelector("#gym-location").innerText = session.location;
  headerContainer.querySelector("#duration").innerText = formatDuration(session.duration);
  let hours = new Date(session.end_time).getHours() - new Date(session.start_time).getHours();
  let minutes = (new Date(session.end_time).getMinutes() - new Date(session.start_time).getMinutes()) / 60;
  headerContainer.querySelector("#actual-duration").innerText = formatDuration(parseFloat(hours) + parseFloat(minutes));
  headerContainer.querySelector("#users-attended").innerText = attended;
  headerContainer.querySelector("#users-missed").innerText = session?.users?.length - attended ?? 0;
  headerContainer.querySelector("#end-time").innerText = formatTimestampToAMPM(session.end_time, true) ?? "NaN";
}
function appendAttendedSessions(data) {
  const attendenceContainer = document.getElementById("attended-container");
  const og_attendence_item = attendenceContainer.querySelector("#attended-item");

  if (data.length === 0) {
    const attended = document.createElement("p");
    attended.style.color = "black";
    attended.innerText = "All users missed this session";
    attendenceContainer.append(attended);
  } else {
    data.forEach((user) => {
      const clonedCard = og_attendence_item.cloneNode(true);
      clonedCard.setAttribute("id", "");
      clonedCard.style.display = "flex";

      clonedCard.querySelector("#attended-name").innerText = user.name;

      attendenceContainer.append(clonedCard);
    });
  }
  og_attendence_item.remove();
}
function appendMissedSessions(data) {
  const missedContainer = document.getElementById("missed-container");
  const og_attendence_item = missedContainer.querySelector("#missed-item");

  if (data.length === 0) {
    const missed = document.createElement("p");
    missed.style.color = "black";
    missed.innerText = "No users missed this session";
    missedContainer.append(missed);
  } else {
    data.forEach((user) => {
      const clonedCard = og_attendence_item.cloneNode(true);
      clonedCard.setAttribute("id", "");
      clonedCard.style.display = "flex";

      clonedCard.querySelector("#missed-name").innerText = user.name;

      missedContainer.append(clonedCard);
    });
  }
  og_attendence_item.remove();
}

function initEditUserBtn(users) {
  openModal(
    {
      baseElement: document.getElementById("btn-container"),
      TriggerBtn: "edit-users-btn",
      _modalWindow: "edit-user-popout",
    },
    () => {
      modalUsers = users;
      appendUsers(users);
      // initSearchUsers(token);
    }
  );
  closeModal({
    TriggerBtn: "close-edit-user-btn",
    _modalWindow: "edit-user-popout",
  });
}
function appendUsers(_users) {
  const attendedContainer = document.getElementById("session-current-user-container");
  const og_user_item = attendedContainer.querySelector("#session-current-user-item");

  let allItems = attendedContainer.querySelectorAll(".summary-user-details.selected");
  allItems.forEach((item, indx) => {
    if (indx !== 0) item.remove();
  });

  _users.forEach((user) => {
    const clonedCard = og_user_item.cloneNode(true);
    clonedCard.setAttribute("id", "");
    clonedCard.style.display = "flex";

    if (!user.attended) {
      clonedCard.style.backgroundColor = "#ff634e";
      clonedCard.querySelector("#session-current-user-delete-btn").style.filter = "grayscale(1)";
    }

    if (user.profile_picture)
      clonedCard.querySelector("#session-current-user-pfp").style.backgroundImage = `url(${user.profile_picture.url})`;

    clonedCard.querySelector("#session-current-user-name").innerText = user.name;

    clonedCard.querySelector("#session-current-user-delete-btn").onclick = () => {
      // ? when user clicks on the delete button,
      // ? remove the user from the array and reappend the users to the modal
      modalUsers = modalUsers.filter((u) => u.user_id !== user.user_id);
      appendUsers(modalUsers);
    };

    attendedContainer.append(clonedCard);
  });
  og_user_item.style.display = "none";
}

function appendSearchUsers(data) {
  let modalContainer = document.getElementById("edit-user-popout");
  let searchContainer = modalContainer.querySelector("#search-item-container");
  const og_search_item = searchContainer.querySelector("#search-item");

  function searchUsers(_users) {
    let allItems = searchContainer.querySelectorAll(".summary-user-details.search-item");
    allItems.forEach((item, indx) => {
      if (indx !== 0) item.remove();
    });

    if (_users.length === 0) {
      const noUser = document.createElement("p");
      noUser.style.color = "black";
      noUser.innerText = "No users with this name found";
      noUser.classList.add("summary-user-details", "search-item");
      noUser.style.display = "flex";
      searchContainer.append(noUser);
    } else {
      _users.forEach((user) => {
        const clonedCard = og_search_item.cloneNode(true);
        clonedCard.setAttribute("id", "");
        clonedCard.style.display = "flex";

        if (user.profile_picture)
          clonedCard.querySelector("#search-item-pfp").style.backgroundImage = `url(${user.profile_picture.url})`;

        clonedCard.querySelector("#search-item-name").innerText = user.name;

        clonedCard.onclick = () => {
          let indx;
          let hasUser = modalUsers.some((e, i) => {
            if (e.user_id === user.user_id) {
              indx = i;
              return true;
            }
          });
          if (!hasUser) {
            // ? current modalUsers does not have this user
            user.attended = true; // ? if coach is adding user, that means he attended session, therefore default attendence is true
            modalUsers.push(user); // ? Add to global users array
            appendUsers(modalUsers);
          } else {
            // ? current modalUsers has this user
            modalUsers[indx].attended = true;
            appendUsers(modalUsers);
          }
          clonedCard.remove();
        };

        searchContainer.append(clonedCard);
      });
    }
  }

  searchUsers(data);
  og_search_item.style.display = "none";
}

function initAddCoachBtn(secondary_coach, token) {
  openModal(
    {
      baseElement: document.getElementById("btn-container"),
      TriggerBtn: "add-coach-btn",
      _modalWindow: "add-coach-popout",
    },
    () => {
      let filterFormBlock = document.getElementById("coach-form-block");
      let coachSelect = filterFormBlock.querySelector("#second-coach-select");

      if (secondary_coach.team_id) {
        let coachAlreadyAddedEl = filterFormBlock.querySelector("#coach-already-added-text");
        coachAlreadyAddedEl.innerText = `Coach ${secondary_coach.name} already added`;
        coachAlreadyAddedEl.classList.remove("hide");
      }

      async function fetchFilterSelectData() {
        let data = await fetchUrl(XANO_COACH_GET_COACHES_URL, "GET", false, token);

        if (data.isFetchOk !== undefined && !data.isFetchOk) {
          console.error(data.message);
          redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
          return;
        }

        removeOptions(coachSelect); // ? remove all webflow skeleton options
        data.forEach((coach) => {
          var opt = document.createElement("option");
          opt.value = coach.id;
          opt.innerHTML = coach.name;
          coachSelect.appendChild(opt);
        });

        coachSelect.removeEventListener("focus", fetchFilterSelectData);
      }

      // ? when the user clicks on the select dropdown, it will
      // ? get program name data and coach data
      coachSelect.addEventListener("focus", fetchFilterSelectData);
    }
  );
  closeModal({
    TriggerBtn: "close-add-coach-btn",
    _modalWindow: "add-coach-popout",
  });
}

function initAddNotesBtn(comments) {
  openModal(
    {
      baseElement: document.getElementById("btn-container"),
      TriggerBtn: "add-notes-btn",
      _modalWindow: "add-notes-popout",
    },
    () => {
      appendComments(comments);
    }
  );
  closeModal({
    TriggerBtn: "close-add-notes-btn",
    _modalWindow: "add-notes-popout",
  });
}
function appendComments(comments) {
  const commentEl = document.getElementById("comments");
  if (comments) {
    commentEl.value = comments;
  }
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("submit", async function (e) {
    e.preventDefault();

    const $form = $(this); // The submitted form
    if ($form.attr("id") === "coach-form-block") {
     const token = getToken();
      if (token) {
        const coachSelectEl = document.getElementById("second-coach-select");

        const xano_input = {
          name: coachSelectEl.options[coachSelectEl.selectedIndex].text,
          coach_id: parseInt(coachSelectEl.value),
        };
        let res = await fetchUrl(XANO_COACH_UPDATE_SECOND_COACH_URL + sessionId, "POST", xano_input, token);
        if (res.isFetchOk !== undefined && !res.isFetchOk) {
          console.error(res.message);
          $form
            .siblings(".w-form-done")
            .hide() // Hide success
            .siblings(".w-form-fail")
            .show() // show failure
            .text("There was an error trying to add the second coach. Please refresh your browser and try again.");

          // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
          return;
        }
        $form
          .hide() // optional hiding of form
          .siblings(".w-form-done")
          .show() // Show success
          .siblings(".w-form-fail")
          .hide(); // Hide failure

        window.location.reload();
      }
    } else if ($form.attr("id") === "notes-form-block") {
     const token = getToken();
      if (token) {
        const commentEl = document.getElementById("comments");
        const xano_input = {
          comment: commentEl.value,
        };
        let data = await fetchUrl(XANO_COACH_UPDATE_COMMENTS_URL + sessionId, "POST", xano_input, token);

        if (data.isFetchOk !== undefined && !data.isFetchOk) {
          console.error(data.message);
          redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
          return;
        }
        window.location.reload();
      } else {
        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show() // show failure
          .text("Failed to add comment. Please refresh your browser and try again.");
      }
    } else if ($form.attr("id") === "search-form-block") {
     const token = getToken();
      if (token) {
        let modalContainer = document.getElementById("edit-user-popout");
        let searchInput = modalContainer.querySelector("#search-input");

        let search = searchInput.value;

        if (search.length === 0) return;

        const xano_input = {
          search_query: `${search}:*`,
        };
        let data = await fetchUrl(XANO_COACH_SESSION_SEARCH_URL + search, "POST", xano_input, token);

        if (data.isFetchOk !== undefined && !data.isFetchOk) {
          console.error(data.message);
          $form
            .siblings(".w-form-done")
            .hide() // Hide success
            .siblings(".w-form-fail")
            .show() // show failure
            .text("Oops, that user may not exist! Just to be safe, please try again after refreshing your browser.");
          return;
        }
        appendSearchUsers(data);
      } else {
        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show() // show failure
          .text("Please fill out all fields");
      }
    } else if ($form.attr("id") === "update-session-form-block") {
     const token = getToken();
      if (token) {
        let xano_input = {};
        xano_input.items = [
          ...modalUsers.map((i) => {
            return {
              user_id: i.user_id,
              attended: i.attended,
            };
          }),
        ];

        let newSessionData = await fetchUrl(XANO_COACH_SESSION_SUMMARY_URL + sessionId, "POST", xano_input, token);

        if (newSessionData.isFetchOk !== undefined && !newSessionData.isFetchOk) {
          console.error(newSessionData.message);
          $form
            .siblings(".w-form-done")
            .hide() // Hide success
            .siblings(".w-form-fail")
            .show() // show failure
            .text("There was an error trying to update the session. Please refresh your browser and try again.");

          // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
          return;
        }

        $form
          .hide() // optional hiding of form
          .siblings(".w-form-done")
          .show() // Show success
          .siblings(".w-form-fail")
          .hide(); // Hide failure

        window.location.reload();
      } else {
        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show() // show failure
          .text("Please fill out all fields");
      }
    }
  });
});
